var _self,loginRes;
// import {  
//     mapMutations  
// } from 'vuex';
export default {
	data() {
		return {
			
		};
	},
	onLoad:function() {
		_self = this;
	},
	onShow() {
		loginRes = _self.checkLogin('../set/set',2);
		if(!loginRes) { return false; }
	},
	methods:{
		//...mapMutations(['logout']),

		navTo(url){
			this.$api.msg(`跳转到${url}`);
		},
		//退出登录
		toLogout(){
			uni.showModal({
			    content: '确定要退出登录么',
			    success: (e)=>{
			    	if(e.confirm){
			    		//this.logout();
						uni.request({
							url: _self.apiServer + 'logout',
							header: {
								'Accept' : 'application/json',
								'Content-Type' : 'application/json',
								'Authorization' : 'Bearer '+uni.getStorageSync('SRAND'),
							},
							method: 'GET',
							success: res => {
								console.log(res);
							}
						});
						uni.removeStorageSync('SUID');
						uni.removeStorageSync('SRAND');
						uni.removeStorageSync('SNAME');
						uni.removeStorageSync('SFACE');
						uni.showToast({
							title: '你已经退出系统！',icon:"none"
						});
						setTimeout(function() {
							uni.reLaunch({ //关闭所有面面，然后跳转
								url:'../index/index'
							})
						}, 1000);
			    	}
			    }
			});
		},
		//switch
		switchChange(e){
			let statusTip = e.detail.value ? '打开': '关闭';
			this.$api.msg(`${statusTip}消息推送`);
		},

	}
}
